import {WinJSDec, strError} from './APP/config';

var name = 'DEVICE_COPY';
var DEVICE_COPY = text => {
  if (typeof text === 'string') {
    WinJSDec(_ => {
      window.WinJSBridge.call('tools', 'copyToClipboard', {text: text}, function (res) {
      });
    });
  } else {
    throw new TypeError(`[${name}] ${strError}`);
  }
};
export default DEVICE_COPY;